import { tick } from "svelte";

const componentCache = new Map();
const componentRegistry = new Map();

/**
 * ng k loader ng cho 1 component
 */
export function registerComponentByName(name, loader) {
    componentRegistry.set(name, loader);
}

/**
 * Auto-register ton b component t 1 folder
 * @param {string}  
 */
export function autoRegister() {
    // Thay ng dn ny cho ng vi project ca bn @components/**/*.svelte
    const modules = import.meta.glob('@components/tabs/details/*.svelte');
    //www/src/components/tabs/details
    for (const [path, loader] of Object.entries(modules)) {
        const name = path.split('/').pop().replace('.svelte', '');
        //console.log(`Auto-register component: ${name} from ${path}`);
        if (!name) continue;
        registerComponentByName(name, loader);
    }
}

/**
 * Load component theo tn, c cache
 */
export async function loadComponentByName(name) {
    if (!name) return null;
    // nu c trong cache th ly lun
    if (componentCache.has(name)) {
        return componentCache.get(name);
    }
    // ly loader t registry
    const loader = componentRegistry.get(name);
    if (!loader) {
        console.warn(` Component ${name} cha c register`);
        return null;
    }
    const { default: component } = await loader();
    componentCache.set(name, component);
    return component;
}

export async function preloadComponents(names = []) {
    for (const name of names) {
        try {
            await loadComponentByName(name);
        } catch (err) {
            console.error(` Li preload ${name}:`, err);
        }
    }
    //console.log(' Preload components done:', names);
}

export function registerComponent(type, loaderFn) {
    if (!type || typeof type !== "string") {
        console.error(" registerComponent: type khng hp l:", type);
        return false;
    }
    if (typeof loaderFn !== "function") {
        console.error(` registerComponent: loaderFn cho "${type}" khng phi function`, loaderFn);
        return false;
    }
    if (componentRegistry.has(type)) {
        console.warn(` registerComponent: Component "${type}"  tn ti, s ghi .`);
    }

    componentRegistry.set(type, loaderFn);
    console.log(` ng k component: "${type}" thnh cng.`);
    return true;
}

/**
 * Xo component khi registry
 * @param {string} type - Tn component
 */
export function unregisterComponent(type) {
    if (!type || typeof type !== "string") {
        console.error(" unregisterComponent: type khng hp l:", type);
        return false;
    }
    if (!componentRegistry.has(type)) {
        console.warn(` unregisterComponent: Khng tm thy component "${type}" trong registry.`);
        return false;
    }

    componentRegistry.delete(type);
    console.log(`  xo component: "${type}".`);
    return true;
}

/**
 * Ly loaderFn theo tn component
 * @param {string} type - Tn component
 * @returns {Function|null} loaderFn hoc null nu khng c
 */
export function getComponent(type) {
    if (!type || typeof type !== "string") {
        console.error(" getComponent: type khng hp l:", type);
        return null;
    }
    if (!componentRegistry.has(type)) {
        console.warn(` getComponent: Khng tm thy component "${type}" trong registry.`);
        return null;
    }
    return componentRegistry.get(type);
}
