<script>
  export let label = 'Import File';
  export let accept = '.txt,.json,.csv'; // Cho php m rng
  export let onImport = (text, file) => {}; // Callback t ngoi truyn vo

  let fileInput;

  function handleClick() {
    fileInput.value = ''; // Xa  c th chn li cng 1 file
    fileInput.click();
  }

  function handleChange(event) {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();

    reader.onload = (e) => {
      const content = e.target.result;
      onImport(content, file); // Gi callback vi ni dung file v object file
    };

    reader.onerror = () => {
      console.error('Li khi c file');
    };

    reader.readAsText(file);
  }
</script>

<!-- Nt import -->
<button on:click={handleClick} class="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition">
  {label}
</button>

<!-- Input n -->
<input bind:this={fileInput} type="file" {accept} on:change={handleChange} class="hidden" />
